#viz {
  height: 600px;
  width: 100%;
}

.select-control {
  display: flex;
  flex-flow:  column;
  font-size: 1.2em !important;
  margin: 10px;
}
.select-control select {
  border-color: #999; /* Safari: You can make the font size bigger in a select element while keeping the standard webkit appearance if you set a border color. Pretty silly, but at least it works */
  height: 28px;
}
.select-button {
  margin-top: 8px;
  background-color: #fff;
  border-width: 0;
  border-radius: 4px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.65);
  height: 28px;
}
.select-control select:hover, .select-control button:hover {
  cursor: pointer;
}
#list-modal {
  z-index: 1200; /* because the leaflet controls (.leaflet-top, .leaflet-bottom) have z-index 1000, and frontendJR header has z-index 1100 */
}
.list-container {
  margin-top: 48px;
  margin-bottom: 16px;
}
.w3-modal-content {
  max-width: 400px;
  margin:  0 auto;
}
#list-modal button {
  margin-bottom: 8px;
}
#list-modal button:hover {
  cursor: pointer;
}
.hide-modal-button {
  background-color: #ccc;
}

@media (max-width: 768px) {
  .w3-modal{
    padding-top: 100px; /* otherwise the top of the model is hidden behind frontendJR header */
  }
}
